.woocommerce-ordering,
.woocommerce-result-count,
.woocommerce-pagination {
  @extend .col-auto;
}

//
// Pagination
//

.woocommerce-pagination {
  ul {
    @extend .justify-content-center;
  }

  i {
    position: relative;
    top: 1px;
  }
}

//
// Scrollable
//

.simplebar ul.products {
  flex-wrap: nowrap;
  margin-bottom: $spacer;

  li {
    max-width: 40%;
  }

  li:first-child {
    padding-left: 0;
  }

  li:last-child {
    padding-right: 0;
  }
}

.simplebar .row .col {
  @include media-breakpoint-down(md) {
    max-width: 40%;
  }
}
