.form-floating > .form-select ~ label,
.form-floating > .form-control ~ label {
}

.form-floating > .form-select.no-selection ~ label,
.form-floating > .form-control.no-input ~ label {
  // Reset Label for No Selection on Form Control and Form Select
  color: inherit;
  transform: none;
}

.product-configurator .form-floating {
  label::after {
    background-color: transparent !important;
  }
  > .form-control,
  > .form-select {
    ~ label + span {
    }
    border-width: 1px;
    border-color: var(--bs-secondary-bg-subtle);
    &:focus {
      border-color: var(--bs-primary);
    }
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out, background-color 0.15s ease-in-out;
    &.no-selection,
    &.no-input {
      // background-color: rgba(var(--bs-primary-rgb), .025);
      // background-color: rgba(var(--bs-secondary-rgb), 0.25);
      ~ label {
        // transform: scale(1.05);

        // --bs-focus-ring-x: 0;
        // --bs-focus-ring-y: 0;
        // --bs-focus-ring-blur: 0;
        // --bs-focus-ring-width: 0.2rem;
        // --bs-focus-ring-color: var(--bs-secondary);
        // box-shadow: var(--bs-focus-ring-x, 0) var(--bs-focus-ring-y, 0) var(--bs-focus-ring-blur, 0) var(--bs-focus-ring-width) var(--bs-focus-ring-color);
        // color: var(--bs-primary);
      }
    }
    &.yes-selection,
    &.yes-input {
      border-color: var(--bs-secondary);
      &:focus {
        border-color: var(--bs-primary);
      }
      // --bs-border-color: var(--bs-secondary);
      // color: var(--bs-black);
      // border-color: var(--bs-secondary);
      // background-color: rgba(var(--bs-secondary-rgb), .1);
      ~ label {
        // font-weight: 400
        // color: var(--bs-secondary-dark);
        // border-color: var(--bs-secondary-dark);
      }
      ~ .option-value-label {
        // transform: scale(1.01);
      }
      // ~ label {
      //   font-weight: 300;
      // }
    }
    &.on-selection,
    &.on-input {
      // border-color: var(--bs-secondary-border-subtle);
      // background-color: rgba(var(--bs-secondary-rgb), 0);
      ~ label {
        // font-weight: 400
        // --bs-focus-ring-x: 0;
        // --bs-focus-ring-y: 0;
        // --bs-focus-ring-blur: 0;
        // --bs-focus-ring-width: 0.2rem;
        // --bs-focus-ring-color: var(--bs-secondary);
